.btn {
  display: inline-flex;
  padding: 22px;
  color: $color-default-white;
  background-color: $color-transparent;
  border: 4px solid $color-default-white;
  cursor: pointer;
  transition: opacity $trans-default;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  &--main {
    min-width: 230px;
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  &:hover,
  &:focus {

    opacity: 0.6;
    border: 4px solid $color-whisper;
    color: $color-whisper;
  }

  &:active {

    opacity: 0.4;
    border: 4px solid $color-whisper;
    color: $color-whisper;
  }
}
